.main {
  background-color: #eff6ff;
  padding: 0;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  color: #2a4a5c;
}

.main__background {
  min-width: 760px;
  min-height: 1000px;
  background-image: url("../img/bg.jpg");
  background-size: cover;
}

@media (max-width: 1440px) {
  .main__background {
    display: none;
  }
}

.main__content {
  display: flex;
  flex-direction: column;
  margin: 110px 75px;
  align-items: flex-start;
}

.main__desc {
  font-size: 20px;
  margin: 0;
  margin-bottom: 80px;
  color: #7b8a92;
}

.main__social {
  margin: 0;
  margin-bottom: 40px;
}

.main__separator {
  align-self: center;
  font-size: 12px;
  position: relative;
  margin: 0;
  margin-bottom: 40px;

  &::before {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    left: -110px;
    top: 7px;
    background-color: #afafaf;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    right: -110px;
    top: 7px;
    background-color: #afafaf;
  }
}

.main__form {
  display: flex;
  flex-direction: column;
  min-width: 530px;
}

.main-contacts-item {
  position: relative;

  & input {
    box-sizing: border-box;
    padding: 0;
    min-height: 55px;
    min-width: 245px;
    border: 1px solid #eeeeee;
    background-color: #eff6ff;
    padding-top: 20px;
    padding-left: 5px;
    margin-bottom: 50px;

    &:focus {
      outline: none;
      border-left-color: #3698fb;
    }
  }

  & label {
    position: absolute;
    font-size: 14px;
    padding: 5px;
    color: #96a5b1;
  }
}

.main-contacts {
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.main__list-of-types {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
}

.main__textarea {
  max-width: 45%;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  color: #96a5b1;
  font-size: 14px;
  border: 1px solid #eff6ff;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 45px;
  resize: none;
  box-shadow: 0 5px 10px -5px #96a5b1;
}

.main__agree {
  margin: 0;
  margin-bottom: 60px;
}

.main__button {
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 4px;
  background-color: #3698fb;
  font-family: "Barlow-Bold", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  color: #ffffff;

  cursor: pointer;

  &:hover,
  &:focus {
    box-shadow: 0 0 5px #3698fb;
  }
}

@media (max-width: 1440px) {
  .main__button {
    align-self: center;
  }
}
